@import url("https://fonts.googleapis.com/css2?family=Playpen+Sans&family=Poppins:wght@400;500;600&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

* {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
}

body {
  background: #fafbff;
}

.container {
  display: flex;
  gap: 71px;
}

main {
  padding-top: 41px;
}

.sidemenu {
  display: flex;
  flex-direction: column;
  padding: 36px 28px 76px 28px;
  max-width: 306px;
  background: #ffffff;
  width: 100%;
  height: 100vh;
}
.sidemenu__header {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-bottom: 65px;
}
.sidemenu__title {
  font-size: 26px;
  color: #000000;
  font-weight: 600;
}
.sidemenu__version {
  font-size: 10px;
  color: #838383;
}
.sidemenu__nav {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
  flex: 1;
}
.sidemenu__link {
  display: flex;
  align-items: center;
  gap: 14px;
  padding: 11px 8px 11px 11px;
  color: #9197b3;
  border-radius: 8px;
  transition: all ease 0.5s;
}
.sidemenu__link svg path {
  transition: all ease 0.5s;
}
.sidemenu__link svg:last-child {
  margin-left: auto;
}
.sidemenu__link:hover, .sidemenu__link_active {
  background: #5932ea;
  color: #ffffff;
}
.sidemenu__link:hover svg path, .sidemenu__link_active svg path {
  stroke: #ffffff;
}
.sidemenu__profile {
  display: flex;
  align-items: center;
  gap: 12px;
}
.sidemenu__profile img {
  max-width: 32px;
  max-height: 32px;
  border-radius: 50%;
}
.sidemenu__profile p {
  color: #757575;
  font-size: 12px;
}

.title {
  font-size: 24px;
  color: #000000;
  margin-bottom: 51px;
}

.dashboard {
  max-width: 968px;
  width: 100vw;
  border-radius: 30px;
  background: #ffffff;
  padding-bottom: 40px;
}
.dashboard__header {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding: 30px 58px 0 38px;
  margin-bottom: 40px;
}
.dashboard__header p {
  color: #16c098;
}
.dashboard__title {
  font-size: 22px;
  color: #000000;
  font-weight: 600;
  margin-bottom: 7px;
}
.dashboard__search {
  max-width: 216px;
  width: 100%;
  position: relative;
}
.dashboard__search img {
  position: absolute;
  top: 7px;
  left: 8px;
}
.dashboard__search input {
  padding: 10px 0 10px 40px;
  border-radius: 10px;
  width: 100%;
  background: #f9fbff;
}
.dashboard__search input::placeholder {
  color: #b5b7c0;
}
.dashboard__content {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-direction: column;
}
.dashboard__grid {
  padding: 0 38px 14px 40px;
  margin-bottom: 24px;
  width: 100%;
  display: grid;
  grid-template-columns: 1.5fr 1fr 1.5fr 2fr 1.5fr 1fr;
  border-bottom: 1px solid #eeeeee;
}
.dashboard__grid_content {
  padding-left: 0;
  padding-right: 0;
  max-width: 888px;
  width: 100%;
}
.dashboard__text {
  padding: 4px 0;
}
.dashboard__text_grey {
  color: #b5b7c0;
}
.dashboard__status {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  border-radius: 4px;
  border: 1px solid;
  width: 80px;
  height: 29px;
}
.dashboard__status_active {
  color: #00b087;
  border-color: #00b087;
  background: rgba(22, 192, 152, 0.3803921569);
}
.dashboard__status_inactive {
  color: #df0404;
  border-color: #df0404;
  background: #ffc5c5;
}
.dashboard__bottom {
  padding: 0 40px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.dashboard__pagination {
  display: flex;
  gap: 12px;
}
.dashboard__item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 24px;
  height: 24px;
  border-radius: 4px;
  border: 1px solid #eee;
  background: #f5f5f5;
  color: #404b52;
  cursor: pointer;
  transition: all ease 0.5s;
}
.dashboard__item_active, .dashboard__item:hover {
  border-color: #5932ea;
  background: #5932ea;
  color: #ffffff;
}